.vux-timeline {
  padding: 1rem;
}
.vux-timeline > ul > li {
  list-style: none;
}
.vux-timeline-item {
  position: relative;
}
.vux-timeline-item-content {
  padding: 0 0 1.5rem 1.2rem;
}
.vux-timeline-item-head,
.vux-timeline-item-head-first {
  position: absolute;
  content: '';
  z-index: 99;
  border-radius: 99px;
}
.vux-timeline-item-head {
  width: 10px;
  height: 10px;
  left: 1px;
  top: 4px;
}
.vux-timeline-item-head-first {
  width: 20px;
  height: 20px;
  left: -4px;
  top: 5px;
}
.vux-timeline-item-tail {
  position: absolute;
  content: '';
  height: 100%;
  width: 2px;
  left: 5px;
  top: 5px;
  background-color: #04BE02;
}
.vux-timeline-item-checked {
  width: 100%;
  position: absolute;
  left: 0;
  top: 0;
  width: 20px;
  height: 20px;
}
.vux-timeline-item-checked.weui-icon-success-no-circle::before {
  font-size: 14px;
  position: absolute;
  left: 3px;
  top: 3px;
  margin: 0!important;
  color: #FFF;
}
.vux-timeline-item-color {
  background-color: #04BE02;
}

.vux-fade-enter-active,
.vux-fade-leave-active {
  opacity: 1;
  -webkit-transition: opacity linear 0.2s;
  -o-transition: opacity linear 0.2s;
  transition: opacity linear 0.2s;
}
.vux-fade-enter,
.vux-fade-leave-to {
  opacity: 0;
}
.vux-dialog-enter-active {
  -webkit-animation: vux-dialog-in 0.5s;
          animation: vux-dialog-in 0.5s;
}
.vux-dialog-leave-active {
  -webkit-animation: vux-dialog-out 0.3s;
          animation: vux-dialog-out 0.3s;
}
@-webkit-keyframes vux-dialog-in {
0% {
    -webkit-transform: scale(1.185);
            transform: scale(1.185);
    opacity: 0;
}
100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
}
}
@keyframes vux-dialog-in {
0% {
    -webkit-transform: scale(1.185);
            transform: scale(1.185);
    opacity: 0;
}
100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
}
}
@-webkit-keyframes vux-dialog-out {
0% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
}
100% {
    -webkit-transform: scale(0.85);
            transform: scale(0.85);
    opacity: 0;
}
}
@keyframes vux-dialog-out {
0% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
}
100% {
    -webkit-transform: scale(0.85);
            transform: scale(0.85);
    opacity: 0;
}
}
.vux-mask-enter,
.vux-mask-leave-active {
  opacity: 0;
}
.vux-mask-leave-active,
.vux-mask-enter-active {
  -webkit-transition: opacity 300ms;
  -o-transition: opacity 300ms;
  transition: opacity 300ms;
}
